@import url(https://fonts.googleapis.com/css2?family=Tangerine:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;500&display=swap);
*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Quicksand', sans-serif;
}

.bg-main {
  height: 100%;
  width: 100%;
  background: url(/shopping-cart/static/media/bg.6353269a.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: fixed;
  top: 0;
  z-index: -2;
}

.bg-overlay {
  background: linear-gradient(to bottom, #0006, transparent);
  position: absolute;
  top: 60px;
  width: 100vw;
  z-index: -1;
}

.nav {
  position: relative;
  height: 60px;
  color: #eee;
  display: flex;
  justify-content: right;
  align-items: center;
  background: linear-gradient(to bottom, #2229, transparent 70%)
}
.nav-items {
  position: relative;
  top: 15px;
}
.nav-items > .nav-item {
  font-family: 'Quicksand', sans-serif;
  font-size: 16px;
  letter-spacing: 2.4px;
  padding: 20px 25px;
}
.nav-item a:hover {
  color: #bbb;
  text-shadow: 0 0 0 #d0d0d0;
}
.nav-item a:active {
  color: #999;
  text-shadow: 0 0 0 #999;
}
.nav-brand {
  position: absolute;
  left: 20px;
  top: 10px;
  font-size: 3em;
  font-family: 'Tangerine', cursive;
}
.nav a {
  text-decoration: none;
  color: #eee;
  text-shadow: 1px 1px 1px #000;
}

@media (max-width: 520px) {
  .nav {
    display: grid;
    grid-template-rows: 80px 40px;
    grid-template-columns: repeat(3, 1fr);
    height: 130px;
    background: linear-gradient(to bottom, #2229, transparent 70%);
  }
  .nav-brand {
    left: 0;
    position: relative;
    width: 100%;
    grid-column: 1 / -1;
    text-align: center;
    font-size: 2.5em;
  }
  .nav-items {
    width: 100vw;
    display: grid;
    grid-auto-flow: column;
    justify-content: space-around;
  }
}
.home-text {
  color: #eee;
  text-shadow: 1px 1px 3px #000;
  margin: 100px 30px 30px;
  padding: 30px;
  background: linear-gradient(to bottom right, #222a, transparent);
  background-color: #2226;
  box-shadow: 5px 10px 30px #fff2;
  border-radius: 15px;
  -webkit-filter: grayscale(1.3);
          filter: grayscale(1.3);
}
