  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 15px;
  background-color: #222a;
  z-index: -1;
  filter: blur(1px);
} */
.home-text h3 {
  font-size: 4em;
  font-family: 'Tangerine', cursive;
}

.home-text p {
  font-size: 24px;
  text-shadow: 1px 1px 2px #222;
  margin: 30px;
}

@media (max-width: 520px) {
  .home-text {
    padding: 20px;
    padding-right: 20px;
    margin: 70px 20px 30px;
  }
  .home-text h3 {
    padding: 15px;
    padding-right: 10px;
    font-size: 3em;
  }
  .home-text p {
    font-size: 20px;
  }
}

.shop-items {
    display: grid;
    justify-content: center;
    grid-template-columns: repeat(auto-fit, minmax(360px, 400px));
    grid-gap: 5px;
    gap: 5px;
    margin-top: 40px;
}

.item-card {
    padding: 10px;
    border-radius: 2px;
    margin: 0px 5px;
    background-color: #fffc;
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 50px 30px 80px;
    grid-template-areas: "name  img"
                         "price img"
                         "btn   img";
                         
}
.item-card > * {
    margin: 12px 0;
    flex-grow: 1;
}

.item-card a {
    grid-area: name;
    text-decoration: none;
    color: #111;
}
.item-card img {
    grid-area: img;
    background: linear-gradient(to bottom right, #0005, #0003);
    width: 100%;
    max-width: 215px;
    margin: 10px;
}
.item-card p {
    grid-area: price;
}
.add-to-cart {
    grid-area: btn;
    align-self: end;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 15px;
    letter-spacing: 2px;
    padding: 10px;
    background-color: #ffa42c;
    border-radius: 8px;
    border: none;
}
.add-to-cart:hover {
    background-color: #f7b853;
}
.add-to-cart:active {
    background-color: #fbb13b;
}

@media (max-width: 520px) {
    .item-card {
        grid-template-columns: 100%;
        grid-template-rows: 170px 50px 30px 70px;
        grid-template-areas: "img"
                             "name"
                             "price"
                             "btn";
    }
}

.wrap-cart {
  margin-top: 40px;
  display: grid;
  grid-template-rows: repeat(auto-fit, 1fr);
  justify-content: center;
  grid-gap: 2px;
  gap: 2px;
}

.item-card-cart {
  order: 2;
}

.cart-remove,
.cart-add {
  padding: 5px;
}

[class^="cart-"] span {
  display: inline-block;
  height: 2px;
  width: 10px;
  background-color: #222;
}

.cart-remove {
  position: relative;
  right: 5px;
  bottom: 3px;
}

.cart-add {
  position: relative;
  left: 0px;
  bottom: 4px;
  padding-left: 0;
}

.cart-add span:first-child {
  transform: rotate(90deg) translateY(-10px);
}

.item-count {
  padding-left: 4px;
  width: 35px;
  height: 24px;
  border: none;
  border-radius: 4px;
  background-color: #fafafa;
  box-shadow: 0 0 12px #0002;
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
  font-size: 13px;
}

.input-unit {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 3px;
  font-size: 10px;
  font-weight: bold;
}

#checkout {
  width: 85vw;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  margin-bottom: 20px;
}

@media (min-width: 520.01px) {
  #checkout {
    grid-template-columns: 100%;
    grid-template-rows: repeat(2, 1fr);
    grid-auto-flow: column;
  }
}

.order-submit {
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 15px;
  letter-spacing: 2px;
  padding: 10px;
  background-color: #ffa42c;
  border-radius: 8px;
  border: none;
}
.order-submit:hover {
  background-color: #f7b853;
}

.order-submit:active {
  background-color: #fbb13b;
}

.detail-wrap {
  height: 100%;
  width: 100%;
  display: grid;
  justify-content: center;
}

.item-detail {
  margin: 80px 20px;
  max-width: 800px
}

#item-detail {
  grid-template-rows: 50px 30px 1fr;
}

#item-detail img {
  max-width: 600px;
}

@media (max-width: 520px) {
  #item-detail {
    grid-template-columns: 100%;
    grid-template-rows: 200px 50px 30px 70px;
  }
  #item-detail img {
    max-width: 250px;
  }
}

.back-link {
  position: fixed;
  bottom: 20px;
  left: 20px;
  background-color: #b4b4b4;
  padding: 12px 30px;
  border: 1px solid #0002;
  border-radius: 5px;
  color: #0f0f0f;
  font-size: 16px;
  font-weight: bold;
  letter-spacing: 2px;
  text-decoration: none;
}
.back-link:hover {
  background-color: #a8a8a8;
}
.back-link:active {
  background-color: #949494ee;
}

.detail-desc {
  padding: 50px 25px;
  padding-bottom: 20px;
  margin: 20px;
  border-top: 2px solid #0002;
  line-height: 28px;
  letter-spacing: 1px;
  grid-column: 1 / -1;
}

@media (max-width: 520px) {
  .detail-desc {
    padding-left: 5px;
    padding-right: 5px;
  }
}
